/**
* Media queries
**/
.varient-options {
  display: flex;
  justify-content: space-between;
  gap: 0 15px;
}
.varient-options .wrapper {
  width: 100%;
}
.varient-options__size, .varient-options__gender {
  display: grid;
  gap: 10px;
  width: 100%;
  max-width: 300px;
  margin: 10px 0;
}
.varient-options__size {
  grid-template-columns: 1fr 1fr 1fr;
}
.varient-options__gender {
  grid-template-columns: 1fr;
}
.varient-options p.option-title {
  font-family: var(--font-family-1);
  font-size: var(--theme-font-p-mob-3);
  color: var(--clr-smoke-2);
  line-height: 30px;
}
.varient-options .button.button--outline {
  padding: 5px 0px;
  width: auto;
  border: 2px solid rgba(48, 48, 48, 0.2);
}
.varient-options .button.button--outline span {
  color: var(--clr-smoke-2);
}
.varient-options .button.button--outline:hover span, .varient-options .button.button--outline.active span {
  color: var(--clr-white);
}
.varient-options .button.button--outline:after {
  background: var(--clr-smoke-2);
}
.varient-options .button.button--outline.active:after {
  transform: translate(0);
}