/**
* Media queries
**/
.quantity-wrapper {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin: 15px 0;
}
@media only screen and (max-width: 1439px) {
  .quantity-wrapper {
    flex-direction: column;
  }
}
.quantity-wrapper .quantity {
  display: flex;
  text-align: center;
  gap: 0 15px;
  width: 100%;
}
@media only screen and (max-width: 1439px) {
  .quantity-wrapper .quantity {
    justify-content: space-between;
    gap: 0;
  }
}
.quantity-wrapper .quantity.out {
  opacity: 0.4;
}
.quantity-wrapper .quantity.out,
.quantity-wrapper .quantity.out * {
  cursor: not-allowed;
}
.quantity-wrapper .quantity.out input {
  pointer-events: none;
}
.quantity-wrapper .quantity input {
  font-family: var(--font-family-1);
  font-size: var(--theme-font-h2-mob-2);
  color: var(--clr-smoke-2);
  line-height: 30px;
  line-height: 30px;
  max-width: 60px;
  text-align: center;
  border: none;
}
@media only screen and (max-width: 1439px) {
  .quantity-wrapper .quantity input {
    max-width: none;
    min-width: 0px;
  }
}
.quantity-wrapper .quantity button {
  border: none;
  background-color: transparent;
  cursor: pointer;
}
.quantity-wrapper .quantity button svg {
  max-width: 16px;
}
.quantity-wrapper .stock {
  display: flex;
  justify-content: space-between;
  width: 100%;
}
.quantity-wrapper .stock p {
  font-family: var(--font-family-1);
  font-size: var(--theme-font-p-mob-5);
  color: var(--clr-smoke-2);
  line-height: 30px;
}
.quantity-wrapper .stock p:nth-of-type(2) {
  font-family: var(--font-family-2);
  opacity: 0.3;
}
.quantity-wrapper .stock p.out {
  opacity: 0.5;
  margin: 0;
}
.quantity-wrapper .stock .sep {
  width: 1px;
  height: 32px;
  background-color: var(--clr-smoke-2);
  opacity: 0.3;
}
@media only screen and (max-width: 1439px) {
  .quantity-wrapper .stock .sep {
    display: none;
  }
}