/**
* Media queries
**/
.pill-button {
  width: 100%;
  max-width: fit-content;
  min-height: 25px;
  display: flex;
  gap: 8px;
  align-items: center;
  position: relative;
  background-color: var(--clr-black);
  padding: 8px;
  margin-bottom: 5px;
}
.pill-button:hover .pill-button__icon {
  transform: rotate(270deg);
}
.pill-button__text {
  font-family: var(--font-family-1);
  font-size: var(--theme-font-h3-mob-4);
  color: var(--clr-white);
  line-height: normal;
  margin: 0;
}
.pill-button__icon {
  opacity: 1;
  transition: all 0.25s ease-in-out;
  z-index: 1;
  transform: rotate(45deg);
  width: 10px;
  height: 10px;
}
.pill-button__icon svg {
  min-width: 10px;
  height: 10px;
}
.pill-button__icon path {
  fill: var(--clr-white);
}