/**
* Media queries
**/
.breadcrumbs-wrapper {
  background-color: var(--clr-grey);
  padding: 6px 12px;
}
@media only screen and (max-width: 479px) {
  .breadcrumbs-wrapper {
    display: none;
  }
}
.breadcrumbs-wrapper .breadcrumbs {
  display: flex;
  align-items: center;
  gap: 0 16px;
  font-family: var(--font-family-2-2);
  font-size: var(--theme-font-p-mob-1);
  color: var(--clr-smoke-2);
  line-height: normal;
  letter-spacing: 0px;
}
@media only screen and (max-width: 767px) {
  .breadcrumbs-wrapper .breadcrumbs {
    flex-wrap: wrap;
    font-size: 12px;
  }
}
.breadcrumbs-wrapper .breadcrumbs li a {
  opacity: 0.5;
  color: var(--clr-smoke-2);
  display: flex;
  align-items: center;
  gap: 8px;
  transition: all 0.3s ease-in-out;
}
.breadcrumbs-wrapper .breadcrumbs li a:hover {
  color: var(--clr-red);
  opacity: 1;
}
.breadcrumbs-wrapper .breadcrumbs li a:hover svg path {
  fill: var(--clr-red);
}
.breadcrumbs-wrapper .breadcrumbs li .svg-wrapper {
  width: 100%;
  max-width: 20px;
}
.breadcrumbs-wrapper .breadcrumbs li .svg-wrapper svg {
  width: 100%;
  height: auto;
}
.breadcrumbs-wrapper .breadcrumbs li .svg-wrapper svg path {
  transition: all 0.3s ease-in-out;
}
.breadcrumbs-wrapper .breadcrumbs .sep {
  width: 1px;
  height: 32px;
  background-color: var(--clr-smoke-2);
  opacity: 0.5;
}