}
.main-product-wrapper .product .swiper-group-wrapper {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  gap: 6px;
  overflow: hidden;
  height: 826px;
}
@media only screen and (max-width: 1199px) {
  .main-product-wrapper .product .swiper-group-wrapper {
