  .main-product-wrapper .product__info--desktop h2 {
    display: none;
  }
}
.main-product-wrapper .product .product-media-grid {
  display: grid;
  grid-gap: 12px;
  margin: 15px 0;
}
.main-product-wrapper .product .product-media-grid--columns, .main-product-wrapper .product .product-media-grid--stacked {
  grid-template-columns: 1fr 1fr;
