  .main-product-wrapper .product .product-media-grid--thumbnails {
    grid-template-columns: repeat(7, 1fr);
  }
}
.main-product-wrapper .product .product-media-grid-img {
  position: relative;
}
.main-product-wrapper .product .product-media-grid-img:hover .svg-wrapper {
  transform: scale(1);
  opacity: 1;
}
.main-product-wrapper .product .product-media-grid-img img {
  width: 100%;
  height: auto;
  border: 2px solid transparent;
}
.main-product-wrapper .product .product-media-grid-img .img-wrapper--zoom {
  cursor: zoom-in;
}
.main-product-wrapper .product .product-media-grid-img .img-wrapper--zoom.active {
  border: 1px solid var(--clr-red);
  cursor: zoom-out;
}
.main-product-wrapper .product .product-media-grid-img .svg-wrapper {
  position: absolute;
  top: 5%;
  right: 5%;
  z-index: 1;
  width: 100%;
  cursor: pointer;
  transition: all 0.3s ease-in-out;
  transform: scale(0);
  opacity: 0;
}
.main-product-wrapper .product .product-media-grid-img .svg-wrapper:hover {
  transform: scale(1.1);
}
.main-product-wrapper .product .product-media-grid-img .svg-wrapper svg {
  width: 100%;
  height: auto;
}
.main-product-wrapper .product .product-media-grid-img .svg-wrapper--full {
  max-width: 30px;
}
.main-product-wrapper .product .product-media-grid-img .svg-wrapper--double {
  max-width: 20px;
}
.main-product-wrapper .product .product-media-grid-img .svg-wrapper--thumbnail {
  max-width: 18px;
}
@media only screen and (max-width: 1199px) {
  .main-product-wrapper .product .product-media-grid-img .svg-wrapper--double, .main-product-wrapper .product .product-media-grid-img .svg-wrapper--thumbnail {
    display: none;
  }
}
.main-product-wrapper .product .product-media-grid-img--double {
  grid-column: span 1;
}
@media only screen and (max-width: 1199px) {
  .main-product-wrapper .product .product-media-grid-img--double {
    grid-column: span 1;
  }
}
.main-product-wrapper .product .product-media-grid-img--full {
  grid-column: -1/1;
}
.main-product-wrapper .product .product-media-grid.mobile {
  display: none;
}
@media only screen and (max-width: 767px) {
  .main-product-wrapper .product .product-media-grid.mobile {
    display: block;
  }
}
@media only screen and (max-width: 767px) {
  .main-product-wrapper .product .product-media-grid.desktop {
    display: none;
  }
}
.main-product-wrapper .product .product-swiper {
  height: 100%;
  cursor: pointer;
}
.main-product-wrapper .product .product-swiper__slide {
