  object-fit: cover;
  object-position: center;
}
.main-product-wrapper .product .product-swiper .slider-controller {
  display: flex;
  justify-content: center;
  align-items: center;
  position: absolute;
  border: none;
  z-index: 1;
  background: var(--clr-grey);
}
.main-product-wrapper .product .product-swiper--main {
