}
.main-product-wrapper .product .product-swiper--main .slider-controller__prev-button svg {
  transform: rotate(180deg);
}
.main-product-wrapper .product .product-swiper--main img {
  height: 100%;
}
@media only screen and (max-width: 1199px) {
  .main-product-wrapper .product .product-swiper--main img {
    height: auto;
  }
}
.main-product-wrapper .product .product-swiper--support {
