}
.main-product-wrapper .product .product-swiper--support img {
  width: 100%;
  height: 100%;
}
@media only screen and (max-width: 1199px) {
  .main-product-wrapper .product .product-swiper--support img {
    height: auto;
  }
}
.main-product-wrapper .product .product-swiper--support .slider-controller__next-button {
  bottom: 0;
  width: 100%;
  padding: 5px 0;
}
@media only screen and (max-width: 1199px) {
  .main-product-wrapper .product .product-swiper--support .slider-controller__next-button {
    max-width: 35px;
